
.slider-height {
    background-image: url(../img/hero/h1_hero.jpg);
    min-height: 830px;
    background-repeat:no-repeat ;
    background-position: center center;
    @media #{$laptop}{
        min-height: 700px;
    }
    @media #{$md}{
        min-height: 600px;
    }
    @media #{$xs}{
        min-height: 470px;
    }
}

.slider-height2{
    min-height: 300px;
    background-repeat:no-repeat ;
    background-position: center center;
    @media #{$xs}{
        min-height: 260px;
    }
}

.slider-area{
    .hero__caption{
        & h1{
            font-size: 70px;
            font-weight: 900;
            margin-bottom: 14px;
            color: #002d5b;
            font-family: $font_1;
            line-height: 1.2;
            text-transform: uppercase;
            margin-bottom: 53px;
            @media #{$lg}{
                font-size: 60px;
                line-height: 1.2;
            }
            @media #{$md}{
                font-size: 50px;
                line-height: 1.2;
            }
            @media #{$xs}{
                font-size: 35px;
                line-height: 1.2;
            }
        }
        & p{
            font-size: 18px;
            line-height: 1.2;
            font-weight: 300;
            margin-bottom: 39px;
            color: $theme_color;
            margin-bottom: 7px;
            text-transform: uppercase;
            @media #{$xs}{
                margin-bottom: 30px;
            }
        }
    }

}

//
.hero-overly {
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        content: "";
        background-color: rgba(1, 10, 28,  0.3);
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: -1;
        background-repeat: no-repeat;
    }
}

// Another Hero Page
.slider-area{
    .hero-cap{
        & h2{
            color: #fff;
            font-size: 50px;
            font-weight: 700;
            text-transform: capitalize;
        }
    }
}
